.info-box-wrapper {
  background: #fff;
  padding: 1.5rem 1.5rem 0.5rem;
  border-left: 0.4rem solid #5ba4a4;
  border-radius: 0.5rem;
  box-shadow: 0 1.5rem 2rem -0.5rem rgba(91, 164, 164, 0.25);
  margin-bottom: 4rem;
}
.img-wrapper {
  display: block;
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  overflow: hidden;
}

.img-wrapper img {
  max-width: 100%;
  height: auto;
  display: block;
  transition: 0.5s ease-out;
}

.info-box-wrapper h4,
.info-box-wrapper h2 {
  margin: 1rem 0;
}

.info-box-wrapper h4 {
  font-weight: 700;
  color: #5ba4a4;
  font-size: 17px;
  letter-spacing: 1px;
}

.info-box-wrapper h2 {
  color: #2c3a3a;
  transition: 0.5s;
  font-size: 19px;
  letter-spacing: 1px;
}

.info-box {
  color: #7b8e8e;
  letter-spacing: 0.5px;
  padding: 0 0 1rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  border-bottom: 1px solid #7b8e8e;
  margin: 0 0 1rem;
}

.dot {
  display: block;
  width: 3px;
  height: 3px;
  border-radius: 50%;
  background-color: #7b8e8e;
}

.card-skills {
  display: flex;
  flex-wrap: wrap;
}

.card-skills li {
  display: flex;
  align-items: center;
  min-height: 2.2rem;
  font-size: 1rem;
  font-weight: 600;
  letter-spacing: 0.5px;
  color: #5ba4a4;
  background: #effafa;
  border-radius: 0.5rem;
  padding: 0 0.7rem;
  cursor: pointer;
  transition: 0.5s ease-in-out;
  margin-bottom: 1rem;
  margin-right: 1rem;
}
